import React from "react";
import {
  BrowserRouter as Router,
  Routes,
  Route,
  Navigate,
  useLocation,
} from "react-router-dom";
import { AnimatePresence, motion } from "framer-motion";
import LandingPage from "./LandingPage";
import MainPage from "./MainPage";
import CompanyPage from "./CompanyPage";
import MenuBar from "./MenuBar";
import TechnologyPage from "./TechnologyPage";
import ProductPage from "./ProductPage";
import CareerPage from "./CareerPage";
import ContactPage from "./ContactPage";
import NewsPage from "./NewsPage";
import IMMC1 from "./IMMC1";
import IMMC from "./IMMC";
import "../style/font.css"

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <KoolMicro />
    </Router>
  );
}

function KoolMicro() {
  const location = useLocation();
  const pageTransition = {
    initial: { opacity: 0 },
    animate: { opacity: 1 },
    exit: { opacity: 0 },
  };

  return (
    <>
      <AnimatePresence mode="wait">
        <motion.div
          key={location.pathname === "CES25" ? "1" : "0"}
          variants={pageTransition}
          initial="initial"
          animate="animate"
          exit="exit"
          transition={{ duration: 1 }}
          className=""
        >
          <Routes>
            <Route path="/CES25" element={<LandingPage />} />
            <Route path="/*" element={<MainComponent />} />
            <Route path="/" element={<Navigate to="/CES25" replace />} />
          </Routes>
        </motion.div>
      </AnimatePresence>
    </>
  );
}

function MainComponent() {
  const location = useLocation();

  const pageTransition = {
    initial: { opacity: 0 },
    animate: { opacity: 1 },
    exit: { opacity: 0 },
  };

  return (
    <div className="w-screen h-[100svh] flex flex-col overflow-hidden">
      <div className="z-50">
        <MenuBar />
      </div>
      <AnimatePresence mode="wait">
        <motion.div
          key={location.pathname}
          variants={pageTransition}
          initial="initial"
          animate="animate"
          exit="exit"
          transition={{ duration: 0.3 }}
          className="flex flex-grow overflow-hidden"
        >
          <Routes location={location}>
            <Route path="/main" element={<MainPage />} />
            <Route path="/company/about-us" element={<CompanyPage />} />
            <Route
              path="/company"
              element={<Navigate to="/company/about-us" replace />}
            />
            <Route path="/company/news" element={<NewsPage />} />
            <Route path="/technology/immc" element={<IMMC />} />
            <Route
              path="/technology"
              element={<Navigate to="/technology/immc" replace />}
            />
            <Route
              path="/product"
              element={<Navigate to="/product/gallery" replace />}
            />
            <Route path="/product/gallery" element={<ProductPage />} />
            <Route path="/career" element={<CareerPage />} />
            <Route path="/contact" element={<ContactPage />} />
          </Routes>
        </motion.div>
      </AnimatePresence>
    </div>
  );
}


export default App;
