function MainPage() {
  let ref = useRef();

  useEffect(()=>{
    if(ref.current) {
      const hash = window.location.hash;
      if (hash) {
        const targetElement = hash
          ? ref.current.querySelector(`[name="${hash.slice(1)}"]`)
          : null;
        if (targetElement) {
          targetElement.scrollIntoView();
        }
      }
    }

    // url 해쉬 추적
    const mainPageRef = ref.current;
    const sections = mainPageRef.querySelectorAll("section");

    const handleScroll = () => {
      const containerRect = ref.current.getBoundingClientRect();

      sections.forEach((section) => {
        const sectionRect = section.getBoundingClientRect();
        const sectionName = section.getAttribute("name");
        if(!sectionName) return;

        // Check if section is centered in the viewport
        if (
          sectionRect.top >= containerRect.top &&
          sectionRect.bottom <= containerRect.bottom
        ) {
          if(window.location.hash !== "#"+sectionName) {
            window.history.replaceState(null, "", `#${sectionName}`);
          }
        }
      });
    };

    mainPageRef.addEventListener("scroll", handleScroll);

    return () => mainPageRef.removeEventListener("scroll", handleScroll);
  },[])

  return (
    <>
      <div
        ref={ref}
        className="flex-grow overflow-y-scroll"
        style={{ scrollSnapType: "y mandatory" }}
      >
        <ScrollToNextSectionButton container={ref.current} />
        <section
          name="1"
          className="w-full h-full"
          style={{ scrollSnapAlign: "start" }}
        >
          <Cover />
        </section>
        <section
          name="2"
          className="w-full h-full overflow-hidden"
          style={{ scrollSnapAlign: "start" }}
        >
          <Content
            title="A Liquid-Cooling Powerhouse"
            imgSrc={require("../assets/main-contents/1.png")}
            content="Now, how well you cool your semiconductors determines how well they perform. Lowering the temperature allows the semiconductor to work properly and even faster, without thermal throttling.<br/><br/>KoolMicro‘s team of experts with 25+ years of experience has therefore developed the Integrated Manifold MicroChannel (IMMC) technology to to cool high-power-density semiconductors up to eight times more effective than conventional cooling method."
            linkText="More about the People behind KoolMicro"
            link="/company/about-us"
          />
        </section>
        <section
          name="3"
          className="w-full h-full overflow-hidden"
          style={{ scrollSnapAlign: "start" }}
        >
          <Content
            title="Anywhere, Everywhere."
            img={
              <>
                <div className="flex justify-start items-center h-full w-full">
                  <div className="h-full sm:w-1/2 w-full grid grid-cols-1 gap-1">
                    <div className="w-full flex items-center justify-center overflow-y-hidden">
                      <img
                        src={require("../assets/cover/cpu.jpg")}
                        className="w-full object-cover"
                        alt=""
                      />
                    </div>
                    <div className="w-full flex items-center justify-center overflow-y-hidden">
                      <img
                        src={require("../assets/cover/gpu.jpg")}
                        className="w-full object-cover"
                        alt=""
                      />
                    </div>
                    <div className="w-full h-full flex items-center justify-center overflow-y-hidden">
                      <img
                        src={require("../assets/cover/server.jpg")}
                        className="w-full object-cover"
                        alt=""
                      />
                    </div>
                    <div className="w-full flex items-center justify-center overflow-y-hidden">
                      <img
                        src={require("../assets/cover/datacenter.jpg")}
                        className="w-full object-cover"
                        alt=""
                      />
                    </div>
                    <div className="w-full flex items-center justify-center overflow-y-hidden">
                      <img
                        src={require("../assets/cover/automotive.jpg")}
                        className="w-full object-cover"
                        alt=""
                      />
                    </div>
                  </div>
                </div>
              </>
            }
            content="IMMC's unique manifold microchannel structure, chip integration method, and flow control method enables uniform cooling of a larger area with lower pumping power at a reasonable price.<br/><br/>
We offer innovative Direct-to-Chip liquid cooling solutions for AI and high-performance semiconductor chips, optimizing performance and dramatically enhancing energy efficiency for data centers, semiconductor manufacturers, and the automotive industry as a global leader and trusted partner."
            linkText="View our Full Product Range"
            link="/product"
          />
        </section>
        <section
          name="4"
          className="w-full h-full overflow-hidden"
          style={{ scrollSnapAlign: "start" }}
        >
          <Content
            title="Patent-Driven Technology."
            img={
              <>
                <div className="w-full h-full">
                  <div className="md:w-1/2 w-full h-full flex justify-center items-center overflow-hidden">
                    <img
                      src={require("../assets/chip2.png")}
                      className="object-cover w-full"
                      alt=""
                    />
                  </div>
                </div>
              </>
            }
            content="KoolMicro’s IMMC technology powers advanced semiconductor cooling by optimizing internal structures for efficient heat transfer. Unlike traditional methods, it leverages innovative fluid dynamics for direct, superior thermal management.<br/><br/>With a Korean-granted patent and multiple U.S. applications in progress, our intellectual property portfolio safeguards the uniqueness and performance of our solutions on a global scale."
            linkText="Learn more about our Tech"
            link="/technology"
          />
        </section>
        <section
          name="5"
          className="flex w-full h-full md:overflow-y-auto overflow-y-scroll"
          style={{ scrollSnapAlign: "start" }}
        >
          <ContactSection />
        </section>
      </div>
    </>
  );
}

export default MainPage;